import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { withTranslation } from 'react-i18next';
import { Tooltip, Icon, message, Button } from 'antd';
import { wsget, host } from '../../ws'
import { getNum, setCookie, storage, session } from '../../util/util';
import Footer from '../../common/Widget/Footer'
import './style.scss';
import Protocol from './Protocol';
// import Perfectinformation from '../../components/Perfectinformation';
import Secondhead from '../../common/Secondhead';
import { reg } from '../../util/util';
import service from './en-service';
import policy from './en-policy';
// import { enc } from '../../util/b64';

@withTranslation() @inject('login') @observer
class Register extends Component {
  constructor(props) {
    super(props);
    this.textRef = React.createRef();
    this.pwdRef = React.createRef();
    this.rep_pwdRef = React.createRef();
    this.codeRef = React.createRef();
    this.verifyRef = React.createRef();
    this.t = this.props.t;
    this.state = {
      focus: null,
      randomnum: null,
      num: 0,
      loading: false,
      isclick: false,
      service_dis: false,
      policy_dis: false,
      codeLoad: false
    };
  }

  componentDidMount() {
    this.getCode();
  }

  componentWillUnmount() {
    this.props.login.reset()
  }

  getCode() {
    this.setState({ codeLoad: false })
    let num = getNum(32);
    let code = `${host()}/imagecode/${num}`
    let img = new Image();
    img.src = code;
    img.onload = () => {
      this.setState({ randomnum: code, num, codeLoad: true })
    }
  }



  //注册
  handleSubmit = e => {
    let key = 'reg';
    e.preventDefault();
    let { isName } = this.props.login;
    let name = this.textRef.current.value;
    let pwd = this.pwdRef.current.value;
    let repwd = this.rep_pwdRef.current.value;
    let code = this.codeRef.current.value;
    if (name === '' || pwd === '') {
      message.error(this.t('AccountPwdEmpty'))
      return;
    } else if (!reg.name.test(name)) {
      message.error(this.t('accountFormatWrong'));
      return;
    } else if (!isName) {
      message.error(this.t('accountExists'));
      return;
    } else if (!reg.pwd.test(pwd)) {
      message.error(this.t('pwdFormat'));
      return;
    } else if (repwd === '') {
      message.error(this.t('entertwoPwds'));
      return;
    } else if (pwd !== repwd) {
      message.error(this.t('twoPwds'));
      return;
    } else if (code === '') {
      message.error(this.t('enterVcode'));
      return;
    }
    this.setState({ loading: true, isclick: true })
    let data = {
      name,
      pwd,
      key: this.state.num,
      code
    }
    message.loading({ content: this.t('InRegistration'),key })
    wsget('user_register', data).then((res) => {
      this.setState({
        loading: false,
        isclick: false
      })
      let userData = { name, pwd }
      wsget('user_login', userData).then((res) => {
        message.success({ content: this.t('SuccesCompleteInfo'), duration: 5, key });
        setCookie('cookie', res.token, 0);
        session.set('token', res.token)
        this.props.login.user_login(res);
        storage.set('user', {...userData,...res});
        storage.set('Z_H_U_DATA', res);
        this.props.history.push(`/perfectinformation/${res.id}`);
      }).catch((res) => {
        message.error({ content: this.t('Registrationfailed'), key });
        this.setState({ loading: false, isclick: false })
      })
    }).catch(() => {
      message.error({ content: this.t('codeErr'), key });
      this.setState({ loading: false, isclick: false })
    })
  };

  go_login = () => {
    this.props.history.push("/login");
  }

  //用户名检测是非存在
  handleUsername = (e) => {
    let name = this.textRef.current.value;
    this.props.login.handleUsername(name);
  }

  input_focus = (txt) => {
    this.setState({ focus: txt })
  }
  input_blur = () => {
    this.setState({ focus: null })
  }

  render() {
    const { t } = this.props;
    const { isName, check_name } = this.props.login;
    const {
      focus,
      randomnum,
      isclick,
      loading,
      service_dis,
      policy_dis,
      codeLoad } = this.state;
    const tooltipNode = () => {
      return (
        <ul>
          <li>{t('regrule2')}</li>
          <li>{t('regrule3')}</li>
        </ul>
      )
    }
    const tooltipNode1 = () => {
      return (
        <ul>
          <li>{t('CaseInsensitive')}</li>
          <li>{t('changeItcode')}</li>
        </ul>
      )
    }

    return (
      <div id='content'>
        <Secondhead
          title={t('Registeredaccountnumber')}
          buyBtn={t('Buyingphotoframes')} />
        <div className='register-container'>
          <div className='register-inner'>
            <div className='register-box'>
              <div className='register'>
                <div className="new-reg-guide-login">
                  <h3>{t('WelcomeRegister')}</h3>
                  <p>{t('haveAccount')}<span id="login_btn" onClick={this.go_login}>{t('login')}</span></p>
                </div>
                <div className='reg-content'>
                  <p className='form_item'>
                    <label className='form_label' htmlFor='user_name'>{t('UserName')}</label>
                    <Tooltip
                      placement="topLeft"
                      overlayClassName='tooltip'
                      visible={focus === 'text'}
                      title={t('regrule1')}>
                      <input
                        id='user_name'
                        className={`form_input text-input ${focus === 'text' ? 'input_focus' : null}`}
                        onFocus={() => { this.input_focus('text') }}
                        onBlur={this.input_blur}
                        onInput={this.handleUsername}
                        type='text'
                        ref={this.textRef}
                        placeholder={t('setUserName')} />
                    </Tooltip>
                    <span className='hasName' style={check_name ? { display: 'inline-block' } : { display: 'none' }}>
                      <Icon style={{ color: 'red' }} type={isName ? "check-circle" : "close-circle"} theme={isName ? "twoTone" : "filled"} />
                    </span>
                  </p>
                  <p className='form_item'>
                    <label className='form_label' htmlFor='user_pwd'>{t('Password')}</label>
                    <Tooltip placement="topRight" overlayClassName='tooltip' visible={focus === 'pwd'} title={tooltipNode}>
                      <input
                        id='user_pwd'
                        className={`form_input pwd-input ${focus === 'pwd' ? 'input_focus' : null}`}
                        onFocus={() => { this.input_focus('pwd') }}
                        onBlur={this.input_blur}
                        type='password'
                        ref={this.pwdRef}
                        placeholder={t('setPassword')} />
                    </Tooltip>
                  </p>
                  <p className='form_item'>
                    <label className='form_label' htmlFor='again_pwd'>{t('RepeatPwd')}</label>
                    <input
                      id='again_pwd'
                      className={`form_input pwd-input ${focus === 'rep_pwd' ? 'input_focus' : null}`}
                      onFocus={() => { this.input_focus('rep_pwd') }}
                      onBlur={this.input_blur}
                      type='password'
                      ref={this.rep_pwdRef}
                      placeholder={t('enterPwdAgain')} />
                  </p>
                  <p className='form_item'>
                    <label className='form_label' htmlFor='verifyCode'>{t('Vcode')}</label>
                    <Tooltip placement="topLeft" overlayClassName='tooltip' visible={focus === 'code'} title={tooltipNode1}>
                      <input
                        id='verifyCode'
                        className={`form_input verifyCode-input ${focus === 'code' ? 'input_focus' : null}`}
                        onFocus={() => { this.input_focus('code') }}
                        onBlur={this.input_blur}
                        type='text'
                        ref={this.codeRef}
                        autoComplete="off"
                        placeholder={t('enterVcode')} />
                    </Tooltip>
                    <span className='button-verifyCodeSend' onClick={this.getCode.bind(this)}>
                      {codeLoad && <img alt='code' width='124' height='38' src={randomnum} />}
                    </span>
                  </p>
                  <p className='form_but'>
                    <Button className="button-reg" loading={loading} onClick={this.handleSubmit.bind(this)}>
                      {isclick ? t('InRegistration') : t('Register')}
                    </Button>
                  </p>
                  <p className='Agree'>
                    {t('meansConsent')}&nbsp;
                    <span onClick={() => { this.setState({ service_dis: true }) }}>{t('ServiceAgreement')}</span>
                    &nbsp;{t('and')}&nbsp;
                    <span onClick={() => { this.setState({ policy_dis: true }) }}>{t('PrivacyPolicy')}</span>
                  </p>
                </div>
              </div>

              <Footer />
            </div>

            <div className='l-top'></div>
            <div className='l-bottom'></div>
          </div>
        </div>

        {service_dis && <Protocol
          title={t('frameservice')}
          content={service}
          close={() => { this.setState({ service_dis: false }) }} />}

        {policy_dis && <Protocol
          title={t('PrivacyPolicy')}
          content={policy}
          close={() => { this.setState({ policy_dis: false }) }} />}
      </div>
    );
  }
}

export default Register;
